html, body {
  width: 100%;
  height: 100%;
  background: #222222;
}
#root{
  width: 100%;
  height: 100%;
}

/* stage start */
.stage {
  position: relative;
  width: 100%;
  height: calc(100% - 30px);
}
/* stage end */


/* image start */
.image-sec {
  position: relative;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-color: #ddd;
}
.img-figure {
  position: absolute;
   width: 320px;
   height: 360px;
   margin: 0px;
   padding: 40px;
   background: #ffffff;
   -webkit-box-sizing: border-box;
           box-sizing: border-box;
}
figcaption{
  text-align: center;
}
.img-title {
  margin: 20px 0 0 0;
  color: #a7a0a2;
  font-size: 16px;
}
/* image end */

/* controller start */
.controller-nav {
  position: absolute;
  width: 100%;
  text-align: center;
  bottom: 30px;
  left: 0;
  z-index: 101;
}
/* controller end*/


